import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import ReactDOM from 'react-dom';
import MDSpinner from 'react-md-spinner';
import { geolocated } from "react-geolocated"
import ReactMapGL, { GeolocateControl, NavigationControl } from 'react-map-gl';

import MainArticlesList from './MainArticlesList'
import Geoloc from '../components/Geolocation'

const MAPBOX_TOKEN = 'pk.eyJ1IjoibHl1Ym92IiwiYSI6ImNqeHEzN3B0cDBtYzYzaHA3Z3FobTN3NWgifQ.ZqX5fDxZ0NJKxX9iAkufaw';

export default class MapContainer extends PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      places: [],
      loading: false,
      viewport: {
        width:"100vw",
        height: 573,
        latitude: 55.7522200,
        longitude: 37.6155600,
        zoom: 1
      },
    };
  }

  componentWillMount() {
    this.setState({
      loading: true
    });

    if ('geolocation' in navigator) {
      console.log('yes')
      navigator.geolocation.getCurrentPosition(position => {
        const currentLatitude = position.coords.latitude;
        const currentLongitude = position.coords.longitude

        console.log('currentLatitude ' + currentLatitude + ' currentLongitude ' + currentLongitude);
        this.setState ({
          viewport: {
            width:"100vw",
            height: 573,
            latitude: currentLatitude,
            longitude: currentLongitude,
            zoom: 8,
          },
        })
      });
    } else {
      console.log('no')
    }

    fetch(`https://travl.dev/api/places/42 `)
      .then(res => res.json())
      .then(places => {
        this.setState({
          loading: false,
          places: places
        }),
        console.log(places)
      })
  }

  render() {
    const { loading, places } = this.state;
    return (
      <div>
        { loading ? <MDSpinner /> :
          <ReactMapGL
            mapboxApiAccessToken={MAPBOX_TOKEN}
            {...this.state.viewport}
            onViewportChange={(viewport) => this.setState({viewport})}

          >

            <GeolocateControl
              positionOptions={{enableHighAccuracy: true}}
              trackUserLocation={true}
              showUserLocation={true}
              fitBoundsOptions={{maxZoom: 8}}
            />
           <NavigationControl />

          </ReactMapGL>
        }
      </div>

    );
  }
}
